import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import Axios from "axios";
import SelectSearch from "react-select-search";
import {
  addCloseFaktur,
  checkReplikasiValidasi,
  deleteFaktur,
} from "../FindIndex";
import Swal from "sweetalert2";
import {
  getListCloseTransaksiPenjualan,
  getListCloseTransaksiPenjualanSelected,
} from "../actions/closeTransaksiPenjualanAciton";
import { gagal } from "../Alert";
import { getUUID } from "../actions/fakturAction";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptoState = (state) => {
  return {
    listValidasiCustomer: state.provinsi.listValidasiCustomer,
    listCloseTransaksiPenjualan: state.provinsi.listCloseTransaksiPenjualan,
    listCloseTransaksiPenjualanSelected:
      state.provinsi.listCloseTransaksiPenjualanSelected,
  };
};
const server = process.env.REACT_APP_BACKEND_URL;
const serverNonMaster = process.env.REACT_APP_BACKEND_URL_NON_MASTER;
class CloseTransaksiPenjualan extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },
        {
          dataField: "noBon",
          text: "Nomor Bon",
        },
        {
          dataField: "namaAktor",
          text: "Nama Aktor",
        },
        {
          dataField: "bruto",
          text: "Bruto(Gr)",
        },
        {
          dataField: "netto",
          text: "Murni(Gr)",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "rp",
          text: "Ongkos(RP)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            var hasil = checkReplikasiValidasi("FakturTerpilih", row.noFaktur);
            if (hasil === -1) {
              return (
                <div className="text-center">
                  <button
                    className="btn btn-warning mr-2"
                    onClick={(e) =>
                      addCloseFaktur(
                        row.noFaktur,
                        row.tanggal,
                        row.noBon,
                        row.supplier,
                        row.bruto,
                        row.netto,
                        row.bonRp,
                        row.titipRp,
                        row.titipGr,
                        row.keterangan,
                        Swal.fire({
                          position: "top-right",
                          icon: "success",
                          text: "Ditambahkan ke List",
                          showConfirmButton: false,
                          timer: 1500,
                        }),
                        this.disabledButton(e)
                      )
                    }
                  >
                    <i className="fa fa-check-circle mr-1"></i>
                    Pilih
                  </button>
                </div>
              );
            } else {
              return (
                <div>
                  <button
                    className="btn btn-warning mr-2"
                    onClick={() =>
                      deleteFaktur(
                        row.noFaktur,
                        this.updateButton(this.state.tipeTransaksi)
                      )
                    }
                  >
                    <i className="fa fa-times mr-1"></i>
                    Batal
                  </button>
                </div>
              );
            }
          },
        },
      ],
      data: [
        {
          tanggal: "23/11/2020",
        },
      ],
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      listSales: [],
      listCustomer: [],
    };
  }

  disabledButton = (e) => {
    e.target.disabled = true;
  };
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getListCloseTransaksiPenjualanSelected());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };

  changeTransaksi = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  componentDidMount() {
    Axios.get(server + "storages2?tipeData=SALES").then((res) =>
      this.setState({
        listSales: res.data,
      })
    );
    Axios.get(server + "customers").then((res) =>
      this.setState({
        listCustomer: res.data,
      })
    );
    this.props.dispatch(getListCloseTransaksiPenjualan());
  }
  onSubmitToko(data) {
    this.props.change("namaToko", data);
  }
  kirimvalidasi() {
    var hasil =
      JSON.parse(localStorage.getItem("closeTransaksiPenjualan")) || [];
    Axios.put(serverNonMaster + "cst/dc/pjl", hasil)
      .then(() =>
        Swal.fire({
          text: "Cancel Transaksi berhasil",
          icon: "success",
          timer: 2000,
          showConfirmButton: false,
          position: "top-right",
        })
          .then(() => localStorage.removeItem("closeTransaksiPenjualan"))
          .then(() => this.props.dispatch(getUUID()))
          .then(() => this.props.dispatch(getListCloseTransaksiPenjualan()))
          .then(() => this.prevStep())
      )
      .catch(() => gagal("Cancel gagal, Mohon periksa Koneksi anda"));
  }
  getListPenjualan(tanggalFrom, tanggalTo, kodeCustomer) {
    this.props.dispatch(
      getListCloseTransaksiPenjualan(tanggalFrom, tanggalTo, kodeCustomer)
    );
  }
  setToko(data) {
    this.props.change("namaToko", data);
    this.setState({
      namaToko: data,
    });
  }
  render() {
    var namaToko = this.state.listCustomer.map((obj) => {
      var payload = {
        value: obj.kodeCustomer,
        name: obj.namaToko,
      };
      return payload;
    });
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-3">
                      <label htmlFor="">Nama Toko</label>
                      <SelectSearch
                        options={namaToko}
                        search
                        placeholder="Silahkan Pilih"
                        onChange={(data) => this.setToko(data)}
                      />
                    </div>
                    <div className="col-lg-3 d-none">
                      <Field
                        name="namaToko"
                        label="Nama Toko"
                        type="text"
                        component={renderField}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="tanggal"
                        label="Tanggal Transaksi"
                        component={renderField}
                        type="date"
                        onChange={(e) =>
                          this.setState({
                            tanggal: e.target.value,
                          })
                        }
                      />
                    </div>
                    <div className="col-lg-3">
                      <label htmlFor="">Tipe Transaksi</label>
                      <Field
                        name="TipeTransaksi"
                        label="Tanggal Transaksi"
                        component="select"
                        className="form-control"
                        onChange={(e) =>
                          this.setState({
                            TipeTransaksi: e.target.value,
                          })
                        }
                      >
                        <option
                          value="
                            DEFAULT"
                          key="
                            DEFAULT"
                        >
                          Silahkan PIlih
                        </option>
                        <option value="UMUM" key="UMUM">
                          UMUM
                        </option>
                        <option value="FICO" key="FICO">
                          FICO
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() =>
                            this.getListPenjualan(
                              this.state.tanggal,
                              this.state.tanggal,
                              this.state.kodeCustomer
                            )
                          }
                        >
                          <i className="fa fa-eye"></i> Lihat Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      {this.props.listCloseTransaksiPenjualan ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.listCloseTransaksiPenjualan}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="id"
                          data={[]}
                          columns={this.state.columns}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next
                          <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12 mt-3">
                      {this.props.listCloseTransaksiPenjualanSelected ? (
                        <ToolkitProvider
                          keyField="id"
                          data={this.props.listCloseTransaksiPenjualanSelected}
                          columns={this.state.columns}
                          search
                        >
                          {(props) => (
                            <div className="form-group">
                              <BootstrapTable
                                {...props.baseProps}
                                striped
                                hover
                                condensed
                                bordered={true}
                                headerClasses="header-table"
                                pagination={paginationFactory()}
                              />
                            </div>
                          )}
                        </ToolkitProvider>
                      ) : (
                        <BootstrapTable
                          keyField="id"
                          data={[]}
                          columns={this.state.columns}
                          striped
                          hover
                          condensed
                          bordered={true}
                          headerClasses="header-table"
                          noDataIndication="Tidak Ada Data"
                        />
                      )}
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.kirimvalidasi()}
                        >
                          <i className="fa fa-times-circle mr-3"></i>
                          Batal Transaksi
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

CloseTransaksiPenjualan = reduxForm({
  form: "CloseTransaksiPenjualan",
  enableReinitialize: true,
})(CloseTransaksiPenjualan);
export default connect(maptoState, null)(CloseTransaksiPenjualan);
